<script>
import Masonry from "masonry-layout";
import ImagesLoaded from "imagesloaded";
export default {
  data() {
    return {
      artworks: [],
      a: [
        0, 5, 7, 9, 13, 14, 15, 20, 22, 24, 28, 29, 30, 35, 37, 39, 43, 44, 45,
        50, 52, 54, 58, 59, 60, 65, 67, 69, 73, 74, 75, 80, 82, 84, 88, 89, 90,
        95, 97, 99,
      ],
      b: [
        1, 6, 11, 16, 21, 26, 31, 36, 41, 46, 51, 56, 61, 66, 71, 76, 81, 86,
        91, 96,
      ],
      c: [2, 3, 4, 8, 10, 12],
      pageLoaded: false,
    };
  },
  methods: {
    getData() {
      this.$prismic.client
        .get({
          predicates: this.$prismic.predicate.at("document.type", "artwork"),
          pageSize: 100,
          orderings: "document.first_publication_date",
        })
        .then((response) => {
          this.artworks = response.results;

          let play = this;
          ImagesLoaded(document.querySelector(".grid"), () => {
            // Get screenwidth
            const screenWidth = document.body.scrollWidth;
            let responsiveGutter;
            if (screenWidth > 768) {
              responsiveGutter = 90;
            } else {
              responsiveGutter = 14;
            }
            // Load Masonry
            let row = document.querySelector("[data-masonry]");
            new Masonry(row, {
              percentPosition: true,
              itemSelector: ".grid-item",
              horizontalOrder: true,
              gutter: responsiveGutter,
              columnWidth: ".grid-item",
              fitWidth: true,
            });
            play.pageLoaded = true;
          });
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

