<script>
import Masonry from "masonry-layout";
import ImagesLoaded from "imagesloaded";
import Plyr from "plyr";
import "plyr/dist/plyr.css";
export default {
  data() {
    return {
      photoset: [],
      pageLoaded: false,
      player: null,
      videoStarted: false,
    };
  },
  methods: {
    getData() {
      this.$prismic.client.getSingle("runway").then((response) => {
        this.photoset = response.data.runway_photo;

        let play = this;
        ImagesLoaded(document.querySelector(".grid"), () => {
          // Get screenwidth
          const screenWidth = document.body.scrollWidth;
          let responsiveGutter;
          if (screenWidth > 768) {
            responsiveGutter = 90;
          } else {
            responsiveGutter = 14;
          }
          // Load Masonry
          let row = document.querySelector("[data-masonry]");
          new Masonry(row, {
            percentPosition: true,
            itemSelector: ".grid-item",
            horizontalOrder: true,
            gutter: responsiveGutter,
            columnWidth: ".grid-item",
            fitWidth: true,
          });
          play.pageLoaded = true;
        });
      });
    },
    loadPlayer() {
      this.player = new Plyr("#player", {
        controls: [
          "play-large",

          "play",
          "progress",
          "current-time",
          "mute",
          "volume",
          "fullscreen",
        ],
        fullscreen: { enabled: false },
      });
      window.player = this.player;

      this.player.on("play", () => {
        if (!this.videoStarted) {
          this.videoStarted = true;
          let min = 1;
          let max = 2700;
          let randomNum = Math.floor(Math.random() * (max - min + 1)) + min;
          this.player.forward(randomNum);
        }
      });
    },
    linkToArtwork(el) {
      if (el) {
        this.$router.push(`/${String(el).toLowerCase()}`);
      }
    },
  },
  mounted() {
    this.loadPlayer();
  },
  created() {
    this.getData();
  },
  unmounted() {
    this.player.destroy();
  },
};
</script>

