);

// Window size
const { width, height } = useWindowSize();
const aspectRatio = computed(() => width.value / height.value);
// Update renderer
function updateRenderer() {
  renderer.setSize(width.value, height.value);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
  thisPixelRatio.value = renderer.getPixelRatio();
  particesMaterial.uniforms.uSize.value = thisPixelRatio.value;
}
// Update camera
function updateCamera() {
  camera.aspect = aspectRatio.value;
  camera.updateProjectionMatrix();
}

// Update canvas on resize
watch(aspectRatio, updateRenderer);
watch(aspectRatio, updateCamera);
camera = new PerspectiveCamera(75, aspectRatio.value, 0.1, 1000);
camera.position.set(0, 0, 20);
scene.add(camera);

/**
 * Loading screen
 */
const overlayGeometry = new PlaneGeometry(2, 2, 1, 1);
// const overlayMaterial = new MeshBasicMaterial({color: 0xff0000 })
const overlayMaterial = new ShaderMaterial({
  transparent: true,
  uniforms: {
    uAlpha: { value: 1 },
  },
  vertexShader: `
    void main()
    {
      gl_Position = vec4(position, 1.0);
    }
