scene.add(overlay);

/**
 * Crops
 */

// Debug
// const gui = new dat.GUI({ width: 340 });
// gui.closed = true;

// Paused Boid Index
let pausedBoidIndex = [];

let boids = [];

// Test boid
function setupFlock(numA) {
  // Popoulate Sprite bois
  var i = 0;
  while (i < numA) {
    boids[i] = new Boid(i);
    i++;
  }
}

const atlasSpriteWidth = 198;
const atlasSpriteHeight = 198;
const atlasSpriteSize = new Vector2(atlasSpriteWidth, atlasSpriteHeight);

function Crop(i) {
  let num = i;

  const spritePosition = new Vector3(
    cropData[num].frame.x / 2020,
    1 - cropData[num].frame.y / 2020 - atlasSpriteWidth / 2020,
    atlasSpriteHeight / 2020
  );

  const spriteMaterial = new ShaderMaterial({
    uniforms: {
      atlas: { value: textureAtlasImage },
      spritePosition: { value: spritePosition },
      atlasSpriteSize: { value: atlasSpriteSize },
      showOutline: {
        value: 0,
      },
    },
    vertexShader: `
        varying vec2 vSpritePosition;
        varying vec2 vUv;
        void main() {
          
          vSpritePosition = uv;
          gl_Position = projectionMatrix * (modelViewMatrix * vec4(0.0, 0.0, 0.0, 1.0) + vec4(position.x, position.y, 0.0, 0.0));
          vUv = uv;
        }
      `,
    fragmentShader: `
        uniform sampler2D atlas;
        uniform vec3 spritePosition;
        uniform vec2 atlasSpriteSize;
        varying vec2 vSpritePosition;
        varying vec2 vUv;
        uniform float showOutline;

        void main() {
    vec2 spriteOffset = vec2(spritePosition.x, spritePosition.y);
    vec2 spriteSize = vec2(spritePosition.z);
    vec2 spritePosition = spriteOffset + vSpritePosition * spriteSize;

    float outline = max(abs(vUv.x - 0.5), abs(vUv.y - 0.5));
    outline = step(0.45, outline) * showOutline;



    gl_FragColor = vec4(outline, outline, outline, 1.0) + texture2D(atlas, spritePosition);

    
  }
      `,

    transparent: true,
  });

  const geometry = new PlaneGeometry(0.2, 0.2);

  // const sprite = new Sprite(spriteMaterial);
  const sprite = new Mesh(geometry, spriteMaterial);

  sprite.userData = cropData[num];
  // sprite.scale.set(0.2, 0.2, 0.2);
  sprite.castShadow = false;
  this.mesh = sprite;
}

function rrand(min, max) {
  return Math.random() * (max - min) + min;
}

// Boid Definition
function Boid(i) {
  // Initial movement vectors
  this.position = new Vector3(0, 0, 0);
  this.velocity = new Vector3(
    rrand(-0.1, 0.1),
    rrand(-0.1, 0.1),
    rrand(-0.1, 0.1)
  );
  this.acceleration = new Vector3(0, 0, 0);
  this.mass = 2;
  this.obj = new Crop(i);
  this.home = new Vector3(0, 0, 0);
  scene.add(this.obj.mesh);
}

let params = {
  separation: 0.001,
  alignment: 0.012,
  cohesion: 0.002,
  centering: 0.000002,
  farFromHome: 1,
  sizeWidth: 1,
  sizeHeight: 1,
  maxSpeed: 0.04,
};

// Run an iteration of the flock
Boid.prototype.step = function (flock) {
  this.accumulate(flock);
  this.update();
  this.obj.mesh.position.set(this.position.x, this.position.y, this.position.z);
  this.obj.mesh.quaternion.copy(camera.quaternion);
};

// gui.add(params, "separation").min(0).max(0.1).step(0.00001).name("separation");

// gui.add(params, "alignment").min(0).max(1).step(0.00001).name("alignment");
// gui.add(params, "cohesion").min(0).max(0.01).step(0.00001).name("cohesion");
// gui
//   .add(params, "centering")
//   .min(0)
//   .max(0.001)
//   .step(0.0000001)
//   .name("centering");

// gui.add(params, "farFromHome").min(0).max(6).step(0.0001).name("far from home");

// gui.add(params, "sizeWidth").min(0.1).max(2).step(0.00001).name("size width");
// gui.add(params, "sizeHeight").min(0.1).max(2).step(0.00001).name("size height");

// gui.add(params, "sizeHeight").min(0.1).max(2).step(0.00001).name("size height");
// gui.add(params, "maxSpeed").min(0.000001).max(1).step(0.000001).name("Max speed");

// Apply Forces
Boid.prototype.accumulate = function (flock) {
  var separation, alignment, cohesion, centering;
  separation = this.separate(flock).multiplyScalar(
    params.separation * this.mass
  );
  alignment = this.align(flock).multiplyScalar(params.alignment);
  cohesion = this.cohesion(flock).multiplyScalar(params.cohesion);
  centering = this.steer(this.home).multiplyScalar(params.centering);
  centering.multiplyScalar(
    this.position.distanceTo(this.home) * this.mass * params.farFromHome
  );
  this.acceleration.add(separation);
  this.acceleration.add(alignment);
  this.acceleration.add(cohesion);
  this.acceleration.add(centering);

  this.acceleration.divideScalar(this.mass);
};

const boundaryBox = {
  x: {
    max: 4.119999885559082,
    min: -4.119999885559082,
  },
  y: {
    max: 3.639999896287918,
    min: -4.599999874830246,
  },
  z: {
    max: 4.119999885559082,
    min: -4.119999885559082,
  },
};

// Update Movement Vectors
Boid.prototype.update = function () {
  this.velocity.add(this.acceleration);

  this.velocity.clampLength(0, params.maxSpeed);

  let spriteSize = 0.2;

  if (this.position.x >= boundaryBox.x.max - spriteSize) {
    this.position.x = boundaryBox.x.max - spriteSize;
    this.velocity.x *= -1;
  } else if (this.position.x <= boundaryBox.x.min + spriteSize) {
    this.position.x = boundaryBox.x.min + spriteSize;
    this.velocity.x *= -1;
  }

  if (this.position.y >= boundaryBox.y.max - spriteSize) {
    this.position.y = boundaryBox.y.max - spriteSize;
    this.velocity.y *= -1;
  } else if (this.position.y <= boundaryBox.y.min + spriteSize) {
    this.position.y = boundaryBox.y.min + spriteSize;
    this.velocity.y *= -1;
  }

  if (this.position.z >= boundaryBox.z.max - spriteSize) {
    this.position.z = boundaryBox.z.max - spriteSize;
    this.velocity.z *= -1;
  } else if (this.position.z <= boundaryBox.z.min + spriteSize) {
    this.position.z = boundaryBox.z.min + spriteSize;
    this.velocity.z *= -1;
  }

  this.position.add(this.velocity);
  this.acceleration.set(0, 0, 0);
};

// Separation Function (personal space)
Boid.prototype.separate = function (flock) {
  var minRange = 20;
  var currBoid;
  var total = new Vector3(0, 0, 0);
  var count = 0;
  // Find total weight of separation
  for (var i = 0; i < flock.length; i++) {
    currBoid = flock[i];
    var dist = this.position.distanceTo(currBoid.position);
    // Apply weight if too close
    if (dist < minRange && dist > 0) {
      var force = this.position.clone();
      force.sub(currBoid.position);
      force.normalize();
      force.divideScalar(dist);
      total.add(force);
      count++;
    }
  }
  // Average out total weight
  if (count > 0) {
    total.divideScalar(count);
    total.normalize();
  }
  return total;
};

// Alignment Function (follow neighbours)
Boid.prototype.align = function (flock) {
  var neighborRange = 20;
  var currBoid;
  var total = new Vector3(0, 0, 0);
  var count = 0;

  // Find total weight for alignment
  for (var i = 0; i < flock.length; i++) {
    currBoid = flock[i];
    var dist = this.position.distanceTo(currBoid.position);
    // Apply force if near enough
    if (dist < neighborRange && dist > 0) {
      total.add(currBoid.velocity);
      count++;
    }
  }
  // Average out total weight
  if (count > 0) {
    total.divideScalar(count);
    total.limit(1);
  }
  return total;
};

// Cohesion Function (follow whole flock)
Boid.prototype.cohesion = function (flock) {
  let neighborRange = 20;
  let currBoid;
  let total = new Vector3(0, 0, 0);
  let count = 0;
  // Find total weight for cohesion
  for (let i = 0; i < flock.length; i++) {
    currBoid = flock[i];
    let dist = this.position.distanceTo(currBoid.position);
    // Apply weight if near enough
    if (dist < neighborRange && dist > 0) {
      total.add(currBoid.position);
      count++;
    }
  }
  // Average out total weight
  if (count > 0) {
    total.divideScalar(count);
    // Find direction to steer with
    return this.steer(total);
  } else {
    return total;
  }
};

Boid.prototype.steer = function (target) {
  let steer = new Vector3(0, 0, 0);
  let des = new Vector3().subVectors(target, this.position);
  let dist = des.length();

  if (dist > 0) {
    des.normalize();
    steer.subVectors(des, this.velocity);
  }
  return steer;
};

// Limit max forces
Vector3.prototype.limit = function (max) {
  if (this.length() > max) {
    this.normalize();
    this.multiplyScalar(max);
  }
};

setupFlock(cropData.length);

/**
 * Cube
 */
// Video loader
let video = ref(null);
let videoMaterial;

let cube = null;

gltfLoader.load("./ayt-cube-13.gltf", (gltf) => {
  cube = gltf;
  cube.scene.scale.set(1, 1, 1);

  cube.scene.children[2].material = videoMaterial;

  cube.scene.children[1].material = new MeshBasicMaterial({
    color: 0x000000,
  });
  cube.scene.children[1].position.y += 0.01;
  originalMatPos = cube.scene.children[1].position.y;
  for (let i = 0; i < cube.scene.children[3].children.length; i++) {
    cube.scene.children[3].children[i].material.transparent = true;
  }

  scene.add(cube.scene);
});

/**
//  * Particles
//  */

let particlesGeometry = new BufferGeometry();

const particesMaterial = new ShaderMaterial({
  uniforms: {
    uSize: {
      value: thisPixelRatio.value,
    },
  },

  vertexShader: `
uniform float uSize;
      void main() {
 vec4 modelPosition = modelMatrix * vec4(position, 1.0);
 vec4 viewPosition = viewMatrix * modelPosition;
 vec4 projectedPosition = projectionMatrix * viewPosition;
 gl_Position = projectedPosition;
 gl_PointSize = 6.0 * uSize;
 gl_PointSize *= (80.0 / - viewPosition.z);

}
  `,
  fragmentShader: `
void main()
{
   
    gl_FragColor = vec4(1.0,1.0,1.0,1.0);
}

  `,
});

const count = 500;
const radius = 100;
const vertices = new Float32Array(count * 3);

for (let i = 0; i < count; i++) {
  const theta = Math.random() * Math.PI * 2;
  const phi = Math.random() * Math.PI * 2;
  const r = radius + Math.random() * (500 - radius);
  vertices[i * 3] = r * Math.sin(theta) * Math.cos(phi);
  vertices[i * 3 + 1] = r * Math.sin(theta) * Math.sin(phi);
  vertices[i * 3 + 2] = r * Math.cos(theta);
}

particlesGeometry.setAttribute("position", new BufferAttribute(vertices, 3));

let particles = new Points(particlesGeometry, particesMaterial);

scene.add(particles);

/**
 * Light
 */
const ambientLight = new AmbientLight(0xffffff, 0.7);

scene.add(ambientLight);

/**
 * Interactivity  click events
 */
let enterCubeTarget = null;
let selectedSprite = null;

let clickedSpriteIndex = null;

// Display video
watch(displayVideo, () => {
  if (displayVideo.value) {
    cube.scene.children[2].material = videoMaterial;
  } else {
    cube.scene.children[2].material = new MeshBasicMaterial({
      color: 0x000000,
    });
  }
});

// Video opacity
let originalMatPos = null;
watch(opacity, () => {
  videoMaterial.opacity = opacity.value / 100;
  videoMaterial.needsUpdate = true;

  if (opacity.value < 100) {
    cube.scene.children[1].position.y = -0.4;
  } else {
    cube.scene.children[1].position.y = originalMatPos;
  }
});

function freezeCube() {
  controls.autoRotate = false;
}
function unfreezeCube() {
  if (!insideCube.value) {
    controls.autoRotate = true;
  }
}
function enterCube() {
  insideCube.value = true;
  point.element.classList.remove("visible");
  // Stop cube rotation
  controls.autoRotate = false;

  const centerVector = new Vector3(0, 0, 0);
  const objectPosition = new Vector3(0, 0, 20);

  const distanceFromCenter = objectPosition.distanceTo(centerVector); // e.g. 10
  const directionFromCenter = centerVector
    .clone()
    .add(objectPosition)
    .normalize(); // Vector(1, 1, 1)

  const cameraOffset = directionFromCenter.clone().multiplyScalar(1); // Vector(5, 5, 5)

  const newCameraPosition = new Vector3(0, 0, 0)
    .add(directionFromCenter) // Vector(1, 1, 1)
    .multiplyScalar(distanceFromCenter) // Vector(10, 10, 10)
    .add(cameraOffset);

  onLerpStart(newCameraPosition);

  enterCubeTarget = new Vector3(0, 0, 4.5);
}
function exitCube() {
  cube.scene.children[1].position.y = originalMatPos;

  setTimeout(() => {
    insideCube.value = false;
  }, 1200);

  point.element.classList.add("visible");
  // Stop cube rotation

  const centerVector = new Vector3(0, 0, 0);
  const objectPosition = new Vector3(0, 0, 4);

  const distanceFromCenter = objectPosition.distanceTo(centerVector); // e.g. 10
  const directionFromCenter = centerVector
    .clone()
    .add(objectPosition)
    .normalize(); // Vector(1, 1, 1)

  const cameraOffset = directionFromCenter.clone().multiplyScalar(1); // Vector(5, 5, 5)

  const newCameraPosition = new Vector3(0, 0, 0)
    .add(directionFromCenter) // Vector(1, 1, 1)
    .multiplyScalar(distanceFromCenter) // Vector(10, 10, 10)
    .add(cameraOffset);

  onLerpStart(newCameraPosition);

  enterCubeTarget = new Vector3(0, 0, 20);
}

let clickMouse = new Vector2();
const onClick = (event) => {
  // Get mouse position
  const canvasSize = canvasRef.value.getBoundingClientRect();
  clickMouse.x = ((event.clientX - canvasSize.left) / width.value) * 2 - 1;
  clickMouse.y = -((event.clientY - canvasSize.top) / height.value) * 2 + 1;

  // Set up raycaster
  raycaster.setFromCamera(clickMouse, camera);
  let thumbIntersects = freezeRaycaster.intersectObjects(
    boids.map((thumb) => thumb.obj.mesh)
  );

  // Display overlay
  if (thumbIntersects.length > 0) {
    pausedBoidIndex = pausedBoidIndex.filter(
      (item) => item !== clickedSpriteIndex
    );

    if (selectedSprite != thumbIntersects[0].object && objClone) {
      camera.remove(objClone);
      objClone = null;
    }

    clickedSpriteIndex = boids
      .map((boid) => boid.obj.mesh.position)
      .indexOf(thumbIntersects[0].object.position);

    boids[
      clickedSpriteIndex
    ].obj.mesh.material.uniforms.showOutline.value = 0.0;

    //Clicking on the boid that is already in focus
    if (!pausedBoidIndex.includes(clickedSpriteIndex)) {
      pausedBoidIndex = [clickedSpriteIndex];

      //Showing the zoom
      if (!objClone) {
        objClone = thumbIntersects[0].object.clone();
        camera.add(objClone);
        objClone.position.set(0, 0, -0.1);
      } else {
        camera.remove(objClone);
        objClone = null;
      }
    }

    selectedSprite = thumbIntersects[0].object;

    const centerVector = new Vector3(0, 0, 0);
    const objectPosition = selectedSprite.position; // e.g. Vector(10, 10, 10)

    const distanceFromCenter = objectPosition.distanceTo(centerVector); // e.g. 10
    const directionFromCenter = centerVector
      .clone()
      .add(objectPosition)
      .normalize(); // Vector(1, 1, 1)

    const cropZoomDistance = width.value > 768 ? 0.2 : 0.32;

    const cameraOffset = directionFromCenter
      .clone()
      .multiplyScalar(cropZoomDistance); // Vector(5, 5, 5)

    const newCameraPosition = new Vector3(0, 0, 0)
      .add(directionFromCenter) // Vector(1, 1, 1)
      .multiplyScalar(distanceFromCenter) // Vector(10, 10, 10)
      .add(cameraOffset);

    onLerpStart(newCameraPosition);

    // Show caption
    currentYear.value = thumbIntersects[0].object.userData.year;
    currentCity.value = thumbIntersects[0].object.userData.country;
    currentSchool.value = thumbIntersects[0].object.userData.school;

    //Upgrade crop texture and load new texture
    if (insideCube.value) {
      showCaption.value = true;

      textureLoader.load(
        require(`@/assets/cube-assets/ayt-hd-compressed/${thumbIntersects[0].object.userData.filename}.jpg`),
        (texture) => {
          thumbIntersects[0].object.material.uniforms.atlas.value = texture;
          thumbIntersects[0].object.material.uniforms.spritePosition.value =
            new Vector3(0, 0, 1);
          thumbIntersects[0].object.material.uniforms.atlasSpriteSize.value =
            new Vector2(1500, 1500);
          thumbIntersects[0].object.material.uniforms.showOutline.value = 0.0;
        }
      );
    }

    // Hide caption
  } else if (selectedSprite) {
    selectedSprite = null;

    pausedBoidIndex = pausedBoidIndex.filter(
      (item) => item !== clickedSpriteIndex
    );
    clickedSpriteIndex = null;

    showCaption.value = false;
    currentYear.value = null;
    currentCity.value = null;
    currentSchool.value = null;

    onLerpStart(new Vector3(0, 0, 4.5));
  } else {
    pausedBoidIndex = [];
  }
  if (!selectedSprite && objClone) {
    camera.remove(objClone);
    objClone = null;
  }
};
// Pointer and mouse events
let drag = false;
function callOnClick(event) {
  if (!drag) {
    onClick(event);
  }
}
if (window.PointerEvent) {
  document.addEventListener("pointerdown", () => (drag = false));
  document.addEventListener("pointermove", () => (drag = true));
  document.addEventListener("pointerup", callOnClick);
} else {
  document.addEventListener("mousedown", () => (drag = false));
  document.addEventListener("mousemove", () => (drag = true));
  document.addEventListener("mouseup", callOnClick);
}

/**
 *  Hover event
 */
let hoveredSpriteIndex = null;
let mouse = new Vector2();
let freezeRaycaster = new Raycaster();

const freezeSprite = (event) => {
  const canvasSize = canvasRef.value.getBoundingClientRect();
  mouse.x = ((event.clientX - canvasSize.left) / width.value) * 2 - 1;
  mouse.y = -((event.clientY - canvasSize.top) / height.value) * 2 + 1;

  freezeRaycaster.setFromCamera(mouse, camera);

  let thumbIntersects = freezeRaycaster.intersectObjects(
    boids.map((thumb) => thumb.obj.mesh)
  );

  if (thumbIntersects.length > 0) {
    document.body.style.cursor = "pointer";

    hoveredSpriteIndex = boids
      .map((boid) => boid.obj.mesh.position)
      .indexOf(thumbIntersects[0].object.position);

    if (clickedSpriteIndex != hoveredSpriteIndex) {
      boids[
        hoveredSpriteIndex
      ].obj.mesh.material.uniforms.showOutline.value = 1.0;
    }
    if (!pausedBoidIndex.includes(hoveredSpriteIndex)) {
      pausedBoidIndex.push(hoveredSpriteIndex);
    }
  } else {
    for (let i = 0; i < pausedBoidIndex.length; i++) {
      boids[
        pausedBoidIndex[i]
      ].obj.mesh.material.uniforms.showOutline.value = 0.0;
    }

    document.body.style.cursor = "";
    if (clickedSpriteIndex) {
      pausedBoidIndex = [clickedSpriteIndex];
    } else {
      pausedBoidIndex = [];
    }
  }
};

/**
 * Camera Lerp
 */
let cameraTarget;
let cameraLerpAlpha;

// Start lerp
const onLerpStart = (target) => {
  if (!camera.position.equals(target)) {
    controls.enabled = false;
    cameraTarget = target;
    cameraLerpAlpha = 0;
  }
};
// Lerping
const lerpCamera = () => {
  camera.position.lerp(cameraTarget, cameraLerpAlpha);
  if (cameraLerpAlpha < 0.18) {
    cameraLerpAlpha += 0.0015;
  } else {
    onLerpComplete();
  }
};
// Lerp end
const onLerpComplete = () => {
  camera.position.lerp(cameraTarget, 1);
  controls.enabled = true;
  cameraTarget = undefined;
  cameraLerpAlpha = false;
  if (enterCubeTarget) {
    onLerpStart(enterCubeTarget);
    enterCubeTarget = null;
  }

  // Show UI after delay
  setTimeout(() => {
    if (insideCube.value) {
      showUi.value = true;
    }
  }, 500);

  // Show UI after delay
  setTimeout(() => {
    if (!insideCube.value) {
      controls.autoRotate = true;
      showUi.value = false;
    }
  }, 2000);
};

watch(insideCube, () => {
  if (insideCube.value) {
    controls.autoRotate = false;
  }
});

/**
 * Render loop
 */
const loop = () => {
  // Update controls
  controls.update();

  // Lerp camera
  if (typeof cameraLerpAlpha === "number") {
    lerpCamera();
  }

  // Boid movement
  for (var i = 0; i < boids.length; i++) {
    if (!pausedBoidIndex.includes(i)) {
      boids[i].step(boids);
    }
  }

  if (objClone) {
    const objPosX = -mouse.x * 0.04;
    const objPosY = -mouse.y * 0.04;
    objClone.position.x += (objPosX - objClone.position.x) / 10;
    objClone.position.y += (objPosY - objClone.position.y) / 10;
  }

  // Fade in tween
  TWEEN.update();

  // Move point
  if (cube) {
    // boundaryBox = new Box3().setFromObject(cube.scene.children[2]);

    // Move door enter point
    let screenPosition = point.position.clone();

    screenPosition.project(camera);

    const intersects = raycaster.intersectObjects(cube.scene.children);
    if (intersects.length === 0) {
      point.element.classList.add("visible");
    } else {
      // point.element.classList.remove("visible");

      const intersectionDistance = intersects[0].distance;
      const pointDistance = point.position.distanceTo(camera.position);

      if (intersectionDistance < pointDistance) {
        point.element.classList.remove("visible");
      } else {
        point.element.classList.add("visible");
      }
    }

    raycaster.setFromCamera(screenPosition, camera);
    const translateX = (screenPosition.x * width.value) / 2;
    const translateY = (-screenPosition.y * height.value) / 2;
    point.element.style.transform = `translateX(${translateX}px) translateY(${translateY}px)`;
  }

  // Update Renderer
  renderer.render(scene, camera);
  requestAnimationFrame(loop);
};

loaded.value = true;

let objClone;

onMounted(() => {
  renderer = new WebGLRenderer({
    canvas: canvasRef.value,
  });

  controls = new OrbitControls(camera, canvasRef.value);
  controls.enableDamping = true;
  controls.autoRotate = true;
  controls.enableZoom = false;

  point = {
    position: new Vector3(0, 0, 5.5),
    element: pointRef.value,
  };

  let videoTexture = new VideoTexture(video.value);
  videoTexture.rotation = Math.PI * 1.5;
  videoTexture.center = new Vector2(0.5, 0.5);

  videoMaterial = new MeshBasicMaterial({
    map: videoTexture,
    side: FrontSide,
    toneMapped: false,
    transparent: true,
  });

  video.value.play();

  updateRenderer();
  renderer.render(scene, camera);
  loop();
  setTimeout(() => {
    canvasRef.value.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  }, 1000);
});
onBeforeUnmount(() => {});

onUnmounted(() => {
  cancelAnimationFrame(loop);
  renderer.dispose();
  scene.traverse((object) => {
    if (!object.isMesh) return;

    object.geometry.dispose();

    if (object.material.isMaterial) {
      cleanMaterial(object.material);
    } else {
      // an array of materials
      for (const material of object.material) cleanMaterial(material);
    }
  });

  document.removeEventListener("mousemove", freezeSprite);
  if (window.PointerEvent) {
    document.removeEventListener("pointerdown", () => (drag = false));
    document.removeEventListener("pointermove", () => (drag = true));
    document.removeEventListener("pointerup", callOnClick);
  } else {
    document.removeEventListener("mousedown", () => (drag = false));
    document.removeEventListener("mousemove", () => (drag = true));
    document.removeEventListener("mouseup", callOnClick);
  }
});

const cleanMaterial = (material) => {
  material.dispose();

  // dispose textures
  for (const key of Object.keys(material)) {
    const value = material[key];
    if (value && typeof value === "object" && "minFilter" in value) {
      value.dispose();
    }
  }
};
</script>

