<template>
  <div class="h-100">
    <!-- Header -->
    <div class="container-fluid">
      <OscarIcons v-if="loaded" />
      <PageHeader :pageName="'Storm From Paradise'" />
    </div>
    <!-- Experience -->
    <div class="container-fluid p-0 m-0">
      <div id="canvas-wrapper" class="position-relative">
    
        <!-- Control panel -->
        <div class="control-panel-wrapper panel-top font-size">
          <div class="container-fluid">
           
            <div
              class="col-auto d-flex justify-content-between align-items-center pt-1"
            >
              <span class="me-2">Vol</span>
              <input
              class="volume-slider"
              @mouseover="hoveringVol = true"
              @mouseleave="hoveringVol = false"
             
                v-model="volume"
                type="range"
                step="0.001"
                min="0"
                max="1"
              />
            </div>
          </div>
        </div>
        <div class="control-panel-wrapper panel-bottom font-size">
          <div class="container-fluid">
            <div class="col-auto pb-1">
              <div class="audio-btn"><span class="the-dot"></span>ON AIR</div>
            </div>
          </div>
        </div>
        <!-- Unmute overlay -->
        <div @click="unMute" v-if="audioMuted" class="un-mute-wrapper">
          <div
            class="container-fluid d-flex justify-content-center align-items-center p-0 m-0 h-100"
          >
            <img class="vol-btn" src="@/assets/img/volume-mute.svg" alt="" />
          </div>
        </div>

        <audio id="audio" ref="audio" controls autoplay muted>
          <source :src="audioSource" type="audio/mpeg" />
        </audio>

        <div
          @mouseover="pauseRotation = true"
          @mouseleave="pauseRotation = false"
          v-for="(flight, index) in flightData"
          :key="index"
          class="flight-card"
          :ref="
            (el) => {
              divs[index] = el;
            }
          "
        >
          <div
            v-if="loaded"
            class="container-fluid d-flex flex-column h-100 p-0 m-0 label-font"
          >
            <div class="col-12 line-bottom row p-0 m-0">
              <div
                class="col-12 d-flex justify-content-start align-items-start ps-1 pt-2 flex-column p-0 m-0 pb-1"
              >
            
                <div class="flight-number">
                  {{ flight.locale }}
                </div>
                 <div class="flight-number">
                  {{ flight.number }}
                </div>
              </div>
            </div>
            
            <div class="col-12 line-bottom row p-0 m-0">
              <div
                class="col-12 d-flex justify-content-start align-items-start ps-1 pt-2 flex-column p-0 m-0 pb-1"
              >
                <div class="flight-label">CALLSIGN</div>
                <div class="flight-number">
                  {{ flight.data[0].Callsign }}
                </div>
              </div>
            </div>

            <!-- First row -->
            <div class="col-12 row p-0 m-0 flex-grow-1">
              <div class="col-12 ps-1 pt-2 p-0 m-0">
                <div class="flight-label">POSITION</div>
                <div class="flight-number pb-1">
                  {{
                    flight.data[Math.ceil(Math.ceil(liveData[index].frameFast))]
                      .Position
                  }}
                </div>

                <div class="flight-label">ALTITUDE</div>
                <div class="flight-number pb-1">
                  {{
                    flight.data[Math.ceil(liveData[index].frameSlow)].Altitude
                  }}
                </div>

                <div class="flight-label">SPEED</div>
                <div class="flight-number pb-1">
                  {{ flight.data[Math.ceil(liveData[index].frameSlow)].Speed }}
                </div>

                <div class="flight-label">DIRECTION</div>
                <div class="flight-number pb-1">
                  {{
                    flight.data[Math.ceil(liveData[index].frameSlow)].Direction
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div ref="cssrenderer" class="cssrenderer"></div>
        <canvas ref="canvasRef"> </canvas>
      </div>
    </div>
    <!-- Text -->
    <div v-if="loaded" class="container-fluid row">
      <div class="col-12 p-0 px-1">
        <ul class="project-tags">
          <li>
            <p>
              Storm from Paradise,
              2022
            </p>
          </li>

          <li>
            <p>DIGITAL ROOM</p>
          </li>
          <li>
            <p>VENICE, ITALY</p>
          </li>
        </ul>
      </div>

      <div v-show="loaded" class="col-12 p-0">
        <h1 class="large-text">
          Storm From Paradise is a soundpiece made by layering distinct sound
          particles from several geographical sources - school playgrounds,
          basketball courts, traffic roundabouts, vegetable gardens - alongside
          aurally interpreted flight data, creating a multitudinous composition.
          <br /><br />
          Taking its name from Walter Benjamins Theses on the Philosophy of
          History, the piece outlines an incidental praxis, turning on themes
          that continue to interest AYT: time, globalisation, and progress.
          <br /><br />
          This work is a recording from a generative iteration of Storm From
          Paradise, presented at the exhibition
          <a target="_blank" href="https://www.stormfromparadise.com"
            >A Storm is Blowing from Paradise, Venice, 2022.</a
          >
        </h1>
      </div>
      <div class="col-12 p-0 px-1">
        <ul>
          <li>
            <p>
              SOUND PRODUCERS: <br />
              STEFANO SCATTOLIN &amp; GUIDO MODANESE (HAMPAR SOUM)
            </p>
            <br> <br>
             <p>
              CREATIVE TECHNOLOGIST: <br />
              MARCO MARTIGNONE
            </p>
          </li>

         
          <li>
            <p>
              SOUND SPATIALISATION: <br />
              BH AUDIO
            </p>
          </li>

          <li>
            <p>
              TECH TEAM COORDINATION: <br />
              WEEXHIBIT
            </p>
          </li>
        </ul>
      </div>
    </div>
    <!-- Menu -->

    <NavMenu v-if="loaded" />
  </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref, computed, watch } from "vue";
import flightData from "@/assets/flight-data.json";
import { useWindowSize } from "@vueuse/core";
import { canvasRef } from "@/assets/global.js";
import {
  PerspectiveCamera,
  Scene,
  Mesh,
  Vector3,
  WebGLRenderer,
  ShaderMaterial,
  MeshBasicMaterial,
  Points,
  BufferGeometry,
  // EdgesGeometry,
  // Color,
  Clock,
  IcosahedronGeometry,
  // WireframeGeometry,
  // LineBasicMaterial,
  // LineSegments,
  Group,
  BufferAttribute,
} from "three";

import {
  CSS3DRenderer,
  CSS3DObject,
} from "three/addons/renderers/CSS3DRenderer.js";

import audioFileOne from "@/assets/cube-assets/sfp-ayt-1.mp3";
import audioFileTwo from "@/assets/cube-assets/sfp-ayt-2.mp3";
import audioFileThree from "@/assets/cube-assets/sfp-ayt-3.mp3";

const loaded = ref(false);
const divs = ref([]);
const liveData = ref([]);
const thisPixelRatio = ref(1);
const pauseRotation = ref(false);

const volume = ref(0);
const audio = ref(null);
const audioSource = ref(null);
const audioFiles = ref([audioFileOne, audioFileTwo, audioFileThree]);
const audioMuted = ref(true);
const hoveringVol= ref(false)


function unMute() {
  audioMuted.value = false;

  // audio.value.muted = false;
  audioEl.muted = false
  volume.value = 0.5;
  audioEl.volume = volume.value;
  // audio.value.volume = volume.value;
  audioEl.play()
  // audio.value.play();


  
}

watch(volume, () => {
 
  audioEl.muted = false
  // audio.value.muted = false;
  audioEl.volume = volume.value;
  // audio.value.volume = volume.value;
  
});

/**
 * THREE
 */
let renderer;
let camera;
let labelRenderer;

const scene = new Scene();
const { width, height } = useWindowSize();
const aspectRatio = computed(() => width.value / height.value);
function updateRenderer() {

  renderer.setSize(width.value, height.value);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
  labelRenderer.setSize(width.value, height.value);
}
// function updateCamera() {
//   camera.aspect = aspectRatio.value;
//   camera.updateProjectionMatrix();
// }
// watch(aspectRatio, updateRenderer);
// watch(aspectRatio, updateCamera);
camera = new PerspectiveCamera(45, aspectRatio.value, 0.1, 1000);

camera.position.z = 5;
scene.add(camera);


const sphereSize = width.value > 768 ? 1.4 : 0.8;

const sphereGeometry = new IcosahedronGeometry(sphereSize, 10, 10);
const shaderMaterial = new MeshBasicMaterial({
  color: 0xffffff,
  wireframe: true,
});
// const shaderMaterial = new ShaderMaterial({

//   vertexShader: `
//   varying vec2 vUv;
//          void main() {
//  vec4 modelPosition = modelMatrix * vec4(position, 1.0);
//  vec4 viewPosition = viewMatrix * modelPosition;
//  vec4 projectedPosition = projectionMatrix * viewPosition;
//  gl_Position = projectedPosition;

//  vUv = uv;

// }
//   `,
//   fragmentShader: `
//  varying vec2 vUv;

// float random (vec2 st) {
//     return fract(sin(dot(st.xy, vec2(12.9898,78.233)))* 43758.5453123);
// }

//   void main()
//   {

//     // vec2 gridUv = vec2(
//     //   floor(vUv.x * 100.0) / 10.0,
//     //   floor(vUv.y * 100.0) / 10.0
//     // );
//     // float strength = random(vUv);

//     float strength = step(0.1, abs(distance(vUv, vec2(0.5)) -0.25));
//     gl_FragColor = vec4(strength,strength,strength,1.0);
//   }
//   `,
// });
const cube = new Mesh(sphereGeometry, shaderMaterial);

// const wireframe = new EdgesGeometry(sphereGeometry);
// const edgesMaterial = new LineBasicMaterial({ color: 0x79f7f4 });
// const edges = new LineSegments(wireframe, edgesMaterial);

// cube.add(edges);
scene.add(cube);

/**
 * Particles
 */
const particlesGeometry = new BufferGeometry();
const particlesCount = 1000;

const vertices = new Float32Array(particlesCount * 3);
const offset = new Float32Array(particlesCount * 1);

for (let i = 0; i < particlesCount * 3; i++) {
  vertices[i] = (Math.random() - 0.5) * 100;
  offset[i] = Math.random();
}

particlesGeometry.setAttribute("position", new BufferAttribute(vertices, 3));
particlesGeometry.setAttribute("tweak", new BufferAttribute(offset, 1));

let elapsedTime;

const particesMaterial = new ShaderMaterial({
  uniforms: {
    uTime: {
      value: elapsedTime,
    },
    uSize: {
      value: thisPixelRatio.value,
    },
  },

  vertexShader: `
uniform float uSize;

attribute float tweak;
varying float vOffset;
      void main() {



          
 vec4 modelPosition = modelMatrix * vec4(position, 1.0);
 vec4 viewPosition = viewMatrix * modelPosition;
 vec4 projectedPosition = projectionMatrix * viewPosition;
 gl_Position = projectedPosition;
 gl_PointSize = 6.0 * uSize;
 gl_PointSize *= (60.0 / - viewPosition.z);

 vOffset = tweak;
}
  `,
  fragmentShader: `
varying float vOffset;
uniform float uTime;
void main()
{
    float flicker = sin(uTime * vOffset) * 2.0;
    vec4 color = vec4(1.0,1.0,1.0, flicker);
    gl_FragColor = color;
}

  `,
});

const stars = new Points(particlesGeometry, particesMaterial);
scene.add(stars);

/**
 *   CSS2D
 */
const cardsGroup = new Group();

function createCards() {
  for (let i = 0; i < flightData.length; i++) {
    liveData.value.push({
      length: flightData[i].data.length,
      frameSlow: 0,
      frameFast: 0,
    });
    // Position around a circle
    const angle = ((Math.PI * i) / flightData.length) * 2;
    const radius = width.value > 768 ? 2.2 : 1;
    const x = Math.cos(angle) * radius; // Get the x position using cosinus
    const z = Math.sin(angle) * radius; // Get the z position using sinus

    const cardScale = width.value > 768 ? 0.003 : 0.002;

    // Create card

    const card = divs.value[i];
    let cssElement = new CSS3DObject(card);

    cssElement.lookAt(camera.position);
    cssElement.position.set(x, 0, z);
    cssElement.scale.set(cardScale, cardScale, cardScale);

    cardsGroup.add(cssElement);
  }
  scene.add(cardsGroup);
}

const rotateAngle = Math.PI * 0.9;

cube.rotation.x = rotateAngle;

// cardsGroup.rotation.x = rotateAngle;
// cardsGroup.rotation.y = rotateAngle;
// cardsGroup.rotation.z = rotateAngle;

const clock = new Clock();

// Random card set up
function generateRandomArray() {
  const length = Math.floor(Math.random() * 3) + 1;
  const result = Array.from({ length }, () =>
    Math.floor(Math.random() * cardsGroup.children.length)
  );

  return result;
}

let startTime = 0;
let showIndex = generateRandomArray();

function getRandomInterval() {
  return Math.floor(Math.random() * (300 - 40 + 1) + 40);
}
let randomInterval = getRandomInterval();

window.onscroll =  () => {  
isPointerDown = false
deltaX =null 
}

const loop = () => {
  // Time
  if(isPointerDown) {
  deltaX = 5
    cube.rotation.y += deltaX / 100;
  cardsGroup.rotation.y += deltaX / 100;
 for (let i = 0; i < cardsGroup.children.length; i++) {
cardsGroup.children[i].rotation.y -= deltaX / 100

 }
  stars.rotation.y += deltaX / 100;
  audioEl.currentTime += deltaX / 10;
  }


  const elapsedTime = clock.getElapsedTime();

  const timeLastTime = elapsedTime;

  // animate
  particesMaterial.uniforms.uTime.value =
    Math.abs(Math.sin(elapsedTime) - timeLastTime) / 2 - elapsedTime * 3;

  // particesMaterial.needsUpdate = true;
  // Renderer

  renderer.render(scene, camera);
  labelRenderer.render(scene, camera);

  // Show random card
  if (!pauseRotation.value && typeof deltaX !== "number") {
    startTime++;
  }
  if (typeof deltaX === "number") {
    const ffVal = Math.abs(deltaX) * 2;
    startTime += ffVal;
  }

  if (startTime > randomInterval) {
    for (let i = 0; i < showIndex.length; i++) {
      cardsGroup.children[showIndex[i]].element.classList.remove("show-flight");
    }

    showIndex = generateRandomArray();

    for (let i = 0; i < showIndex.length; i++) {
      cardsGroup.children[showIndex[i]].element.classList.add("show-flight");
    }

    randomInterval = getRandomInterval();
    startTime = 0;
  }

  // Rotation
  const rotation = Math.PI * 0.0003;

  if (typeof deltaX !== "number") {
    cube.rotation.y += rotation * 5;
    stars.rotation.y += rotation;
  } 

  if (
    cardsGroup.rotation &&
    !pauseRotation.value &&
    typeof deltaX !== "number"
  ) {
    cardsGroup.rotation.y += rotation;
  }

  // Cycle through timestamps
  for (let i = 0; i < cardsGroup.children.length; i++) {
    let pos = new Vector3();
    cardsGroup.children[i].getWorldPosition(pos);

    cardsGroup.children[i].element.style.backgroundColor = `rgba(0,0,0,${
      (pos.z + 1.1) / 2.2
    })`;
    if (!pauseRotation.value && typeof deltaX !== "number") {
      cardsGroup.children[i].rotation.y -= rotation;
    } 
   
    const slowDataSpeed = 0.1;
    liveData.value[i].frameSlow += slowDataSpeed;
    if (liveData.value[i].frameSlow > liveData.value[i].length - 1) {
      liveData.value[i].frameSlow = 0;
    }
    const fastDataSpeed = 0.2;
    liveData.value[i].frameFast += fastDataSpeed;
    if (liveData.value[i].frameFast > liveData.value[i].length - 1) {
      liveData.value[i].frameFast = 0;
    }
  }

  // Loop
  requestAnimationFrame(loop);
};

const cssrenderer = ref(null);
function onMouseMove(evt) {
  if(!hoveringVol.value) {
   if (!mouseDown) {
      return;
    }
    evt.preventDefault();
    deltaX = evt.clientX - mouseX;
    mouseX = evt.clientX;
    rotateScene(deltaX);
  }
 
  }
let isPointerDown = false
  function onPointerDown() {
isPointerDown = true 
  }
  function onPointerUp(){
    isPointerDown  = false
    deltaX = null
  }

  function onMouseDown(evt) {
     if(!hoveringVol.value) {
    evt.preventDefault();
    mouseDown = true;
    mouseX = evt.clientX;
    // mouseY = evt.clientY;
     }
  }

  function onMouseUp(evt) {
     if(!hoveringVol.value) {
    deltaX = null;
    evt.preventDefault();
    mouseDown = false;
    }
  }

let audioEl;
onMounted(() => {
  loaded.value = true;

  renderer = new WebGLRenderer({
    antialias: true,
    canvas: canvasRef.value,
    setPixelRatio: Math.min(window.devicePixelRatio, 2),
  });

  labelRenderer = new CSS3DRenderer();
  labelRenderer.domElement.style.position = "absolute"
  labelRenderer.domElement.style.zIndex = "4"
  labelRenderer.domElement.style.height = "100%"
  labelRenderer.domElement.style.width = "100%"
  labelRenderer.domElement.style.top = "0"
    labelRenderer.domElement.style.bottom = "0"
    labelRenderer.domElement.style.margin = "auto"
  document.getElementById('canvas-wrapper').appendChild(labelRenderer.domElement);

  createCards();

  //
  thisPixelRatio.value = renderer.getPixelRatio();
  particesMaterial.uniforms.uSize.value = thisPixelRatio.value;
  particesMaterial.needsUpdate = true;

  // Audio
  const randomFileIndex = Math.floor(Math.random() * 3);
  audioSource.value = audioFiles.value[randomFileIndex];

  // audio.value.currentTime = Math.floor(Math.random() * (2000 - 0 + 1) + 0);
     audioEl = document.getElementById('audio')
    audioEl.currentTime = Math.floor(Math.random() * (2000 - 0 + 1) + 0);
audioEl.muted = true
  // audio.value.muted = true;

  /**
   *   CSS2D label test
   */

  updateRenderer();
  renderer.render(scene, camera);
  labelRenderer.render(scene, camera);
  loop();

  // Scroll down
  setTimeout(() => {
    canvasRef.value.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  }, 1000);

  

 
if (window.PointerEvent && width.value < 768) {
  document.addEventListener("pointerdown", onPointerDown);

  document.addEventListener("pointerup", onPointerUp);
} else {
  document.addEventListener("mousedown", onMouseDown);
  document.addEventListener("mousemove", onMouseMove);
  document.addEventListener("mouseup", onMouseUp);
}


 
  // }
});
let mouseDown = false;
let mouseX = 0;
let deltaX;

function rotateScene(deltaX) {
    
  // console.log(audioEl.currentTime)
  cube.rotation.y += deltaX / 100;
  cardsGroup.rotation.y += deltaX / 100;
 for (let i = 0; i < cardsGroup.children.length; i++) {
cardsGroup.children[i].rotation.y -= deltaX / 100

 }
  stars.rotation.y += deltaX / 100;
audioEl.currentTime += deltaX / 10;
  // audio.value.currentTime += deltaX / 10;
}

onUnmounted(() => {
  cancelAnimationFrame(loop);

  renderer.dispose();

  scene.traverse((object) => {
    if (!object.isMesh) return;

    object.geometry.dispose();

    if (object.material.isMaterial) {
      cleanMaterial(object.material);
    } else {
      // an array of materials
      for (const material of object.material) cleanMaterial(material);
    }
  });
 document.removeEventListener("mousedown", onMouseDown);
  document.removeEventListener("mousemove", onMouseMove);
  document.removeEventListener("mouseup", onMouseUp);


});

const cleanMaterial = (material) => {
  material.dispose();

  // dispose textures
  for (const key of Object.keys(material)) {
    const value = material[key];
    if (value && typeof value === "object" && "minFilter" in value) {
      value.dispose();
    }
  }
};
</script>

