<template>
  <StormFromParadise :key="resize" />
</template>

<script>
import StormFromParadise from "@/components/projects/storm/StormFromParadise.vue";

export default {
  name: "StormView",
  components: {
    StormFromParadise,
  },
  data() {
    return {
      resize: false,
    };
  },
  methods: {
    resizeToggle() {
      this.resize = !this.resize;
    },
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.resizeToggle());
    });
  },
  unmounted() {
    window.removeEventListener("resize", this.resizeToggle());
  },
};
</script>
