<template>
  <div>
    <div class="container-fluid">
      <PageHeader :pageName="'Artwork Description'" />
      <OscarIcons v-if="pageLoaded" />
      <div class="row">
        <!-- Text -->
        <div class="col-12 mb-5">
          <p v-if="pageLoaded" class="large-text">{{ title }}</p>
        </div>
        <div class="col-12 mb-5">
          <p class="large-text"><prismic-rich-text :field="text" /></p>
        </div>
        <!-- Gallery -->
        <div class="col-12 row m-0 d-flex justify-content-between">
          <div class="col-md-6 col-12 p-0 d-flex align-items-center">
            <img
              @click="cycleImages"
              class="img-fluid gallery-image"
              :src="biggy.url"
              alt=""
            />
          </div>

          <div
            class="col-md-4 col-12 row justify-content-md-end justify-content-between m-0 p-0"
          >
            <div
              @click="updateBiggy(image)"
              v-for="(image, index) in images"
              :key="index"
              class="col-md-7 mt-3 mt-md-0 mb-md-3 col-4 p-0 gallery-image"
              :class="whichPadding(index)"
            >
              <img
                class="img-fluid lazyload"
                :data-src="`${image.url}?q=0`"
                :height="image.dimensions.height"
                :width="image.dimensions.width"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>

    <NavMenu />
  </div>
</template>
<script>
export default {
  data() {
    return {
      pageLoaded: false,
      title: [],
      text: [],
      images: {},
      biggy: [],
    };
  },
  methods: {
    getArtwork(uid) {
      this.$prismic.client.getByUID("artwork", uid).then((artwork) => {
        // Load text
        this.title = artwork.data.code[0].text;
        this.text = artwork.data.web_caption;

        // Load images
        this.images.imageA = artwork.data.image_a;
        this.images.imageB = artwork.data.image_b;
        this.images.imageC = artwork.data.image_c;
        this.biggy = this.images.imageA;

        // Render text
        this.pageLoaded = true;
      });
    },
    updateBiggy(e) {
      this.biggy = e;
    },
    cycleImages() {
      if (this.biggy === this.images.imageA) {
        this.biggy = this.images.imageB;
      } else if (this.biggy === this.images.imageB) {
        this.biggy = this.images.imageC;
      } else {
        this.biggy = this.images.imageA;
      }
    },
    whichPadding(index) {
      if (index === "imageA") {
        return "pe-1 p-md-0";
      } else if (index === "imageB") {
        return "px-1 p-md-0";
      } else {
        return "ps-1 p-md-0";
      }
    },
  },

  created() {
    this.getArtwork(this.$route.params.uid);
  },
};
</script>
<style scoped>
.gallery-image:hover {
  cursor: pointer;
}
</style>