<script>
export default {
  data() {
    return {
      canvases: [],
      stillLoading: false,
    };
  },
  methods: {
    getData() {
      this.$prismic.client.getSingle("canvas").then((response) => {
        const getRandom = (arr, n) => {
          var result = new Array(n),
            len = arr.length,
            taken = new Array(len);
          if (n > len)
            throw new RangeError(
              "getRandom: more elements taken than available"
            );
          while (n--) {
            var x = Math.floor(Math.random() * len);
            result[n] = arr[x in taken ? taken[x] : x];
            taken[x] = --len in taken ? taken[len] : len;
          }
          return result;
        };
        this.canvases = getRandom(response.data.canvases, 3);
        this.stillLoading = true;
      });
    },
  },

  created() {
    this.getData();
  },
};
</script>
